body{
  background-color: rgb(34, 33, 33);
  color: rgb(255, 255, 255);
}


.hero{
  height: 24vh;
  width: 90vw;
  text-align: center;
  margin-bottom: 5vh;
}
.hero h1{
  margin-top: 25vh;
  color: grey;
}
video{
  width: 100vw;
  height: 20vh;
  object-fit: cover;
  position: absolute;
  margin-left: -6.5vw;
  z-index: -100;
  display: block;
  margin-top: -35vh;
}
.search-movie{
  position: absolute;
  margin-top: 33vh;
  background-color: rgb(214, 210, 210);
  width: 40vw;
  margin-left: 1.4vw;
  height: 4vh;
  font-size: 1.5rem;

}

.movies-list img{
  object-fit: cover;
  height: 20rem;
  width: 15rem;
  align-self: center;
}
.movie-card{
  width: 15rem;
  margin: 1rem;
}
.container{
  max-width: 90vw;
}
.row{
  margin-left: 3.5rem;
}

.error{
  margin: 20vh 30vw; 
}
.error img{
  height: 30vh;
}
.return{
  display: block;
  width: 20vw;
  margin: 5vh;
}
.detail{
  margin: 20vh 10vw;
  /* display: flex; */
}

.detail img{
  width: 30vw;
  height: 60vh;
  object-fit: cover;
  margin-right: 3vw;
}
.detail p{
  text-align: justify;
}
.detail-links{
  /* display: flex; */
  width: 10vw;
  margin-bottom: 2vh;
  margin-right: 2vh;
  text-align: center;
}

.nav-links{
  color: inherit;
  margin-right: 2vw;
}

.nav-links:hover{
  color: inherit;
  text-decoration: none;
}

.title{
  color: yellow;
  font-size: 3rem;
}

.add-button{
  margin-top: 1.5vh;
  font-size: 1.6rem;
}
.modal{
  color: black;
}
.button-edit{
  color: grey;
}
label{
  color: black;
}
.top-button-edit{
  padding-right: 38vw;
}
.dark{
  color: grey;
  background-color: rgb(34, 33, 33);
  border-bottom: 0.2rem solid grey;
}
.edit{
  margin: 2rem;
}
.save:hover{
  text-decoration: none;
  color: inherit;
}
.card-btn{
  margin-right: 1.25rem;
}


@media (max-width: 600px) {
  .movies-list img{
    object-fit: cover;
    height: 10rem;
    width: 9.5rem;
    align-self: center;
  }
  .movie-card{
    width: 9.6rem;
    margin: 0.3rem;
  }
  .hero{
    height: 30vh;
    width: 90vw;
    text-align: center;
    margin-bottom: 14vh;
  }
  .hero h1{
    margin-top: 13vh;
    position: absolute;
    /* z-index: 300; */
  }
  video{
    width: 100vw;
    height: 20vh;
    object-fit: cover;
    position: absolute;
    margin-left: -8.5vw;
    z-index: -100;
    display: block;
    margin-top: 8vh;
  }
  .search-movie{
    margin-top: 37vh;
  }
  .detail img{
    width: 80vw;
    height: 60vh;
  }
  .error img{
    margin-left: -11vh;
  }
  .card-btn{
    margin-bottom: 0.4rem;
  }

}

@media (min-width: 1281px) {
  .hero{
    
    margin-bottom: 15vh;
  }
  .detail{
    display: flex;
  }
  .row{
    margin-left: 1rem;
  }
}

@media (min-width: 1025px) and (max-width: 1280px) {
  video{
    height: 30vh;
  }
 
  .detail{
    display: flex;
  }
  
  .search-movie{
    margin-top: 23vh;
    margin-left: 6vw;

  }
  .top-button-edit{
    padding-right: 34vw;
  }
  .error img{
    margin-left: -6%;
  }
  

}



@media (min-width: 768px) and (max-width: 1024px){
  
  .hero{
    height: 14vh;
    margin-bottom: 9vh;
  }
  video{
    height: 49vh;
  }
  .hero h1{
    margin-top: 17vh;
  }
  .search-movie{
    margin-top: 18vh;
  }
  .detail{
    margin-top: 10vh;
  }
  .detail img{
    width: 50vw;
  }
  .top-button-edit{
    padding-right: 61vw;
  }
  .error img{
    margin-left: -9vh;
  }
  .search-movie{
    margin-left: 7.1vw;
  }
}


@media (min-width: 320px) and (max-width: 480px) {
   .detail{
     margin-top: 14vh;
   }
   .detail-links{
    width: 20vw;
   }
   .title{
     font-size: 1.5rem;
   }
   .error h1{
     font-size: 1.5rem;
   }
   .row {
      margin-left: -4.9vw;
    }
    .search-movie{
      margin-left: -3.9vw;
      font-size: 1rem;
    }
}